<template>
    <ItemDetail :previewHeight="null" :aspectRatio="'1 / 1'" v-if="data.ready">
        <template v-slot:background>
            <Img
                v-if="data.item?.cover"
                :preview="data.item?.cover?.crop"
                :original="data.item?.cover?.original"
                :alt="data.item.title"
                :skeleton="true"
            />
        </template>
        <template v-slot:preview>
            <div class="title"
                v-if="data.item?.title"
                @click="openArtist"
            >{{ data.item.name }}</div>

            <div class="buttons">
                <Button :name="t('player.shuffle')" class="tertiary active size-m" icon="random"/>
                <Button :name="t('player.play')" class="size-m" icon="play"/>
            </div>
        </template>
        <template v-slot:toolbar>
            <IconButton
                icon="heart"
                class="tertiary size-xl"
                :class="{ invert: others.theme !== 'dark' }"
                @click="like"
            />

            <IconButton
                icon="tg-stars"
                class="tertiary size-xl"
                @click="like"
            />

            <IconButton
                icon="apps"
                class="tertiary size-l"
                :class="{ invert: others.theme !== 'dark' }"
                @click="openKebab"
            />
        </template>
        <template v-slot:detail>
           <Tracks
                v-if="data.item?.id"
                :getById="data.item.id"
                :loadMore="false"
                :source="{
                    store: 'releases',
                    fnName: 'getTracks'
                }"
                :title="{ text: data.item.title }"
            />
            

            <template v-for="item in primaryArtists" :key="item.id">
                <Releases
                    type="row"
                    :take="8"
                    :params="{
                        contributors: item.id
                    }"
                    :sort="null"
                    :title="{ text: t('blocks.releases.more-from-author', { name: item.name }) }"
                    handler="release"
                />
            </template>


            <!-- <ArtistPreviewCard :artist="data.item" v-if="data.item?.id" /> -->
        </template>
    </ItemDetail>

    <Ready :page="`release-${props.id}`" :init="init" />
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import ItemDetail from 'ðŸ”—/components/ui/ItemDetail.vue';
    import ArtistPreviewCard from '@/components/ui/ArtistPreviewCard.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';

    import Releases from 'ðŸ”—/components/blocks/Releases.vue';
    import Tracks from 'ðŸ”—/components/blocks/Tracks.vue';
    import { useI18n } from 'vue-i18n';
    import moment from 'moment';
    import { message } from 'ðŸ”—/utils/utils';

    const { t } = useI18n();

    const locale = stores.locale();
    const tg = stores.tg();
    const modals = stores.modals();
    const others = stores.others();

    const props = defineProps({
        id: { type: String }
    });

    const data = reactive({
        ready: false,
        item: null
    });

    const primaryArtists = computed(() => {
        if (!data.item?.contributors?.length) {
            return [];
        }

        return data.item.contributors.filter(item => item.role === 'PRIMARY' && item.state === 'CONFIRMED').map(item => item.artist);
    });

    const like = (e) => {
        let button = e?.target?.closest('button');

        if (button) {
            button.classList.remove('dunk');
            setTimeout(() => {
                button.classList.add('dunk');
            }, 10);
        }

        message.info(t('common.soon'))
    }

    const openKebab = () => {
        // modals.open('artist-kebab', { data: { code: 'cmhryr5hl014coi13tbvq6rxm' || props.code } })
    }

    const init = async () => {
        data.item = await api.releases().get(props.id);
        data.ready = true;

        console.log(data.item)
    }

    const openArtist = () => {
        // modals.open('artist', { data: { code: 'cmhryr5hl014coi13tbvq6rxm' || props.code } })
    }

    onMounted(async () => {
        tg.showBackButton();
        tg.showSettingsButton(() => {
            // modals.open('artist-kebab', { data: { code: 'cmhryr5hl014coi13tbvq6rxm' || props.code } })
        });
    });

    onBeforeUnmount(() => {
        tg.hideSettingsButton();
        tg.hideBackButton();
    });
</script>


<style lang="scss" scoped>
    .title {
        font: var(--font-h4);
        color: white; //var(--white-500);
        font-weight: bold;
        text-align: center;
        -webkit-text-stroke: 1px rgb(0, 0, 0, .2);
    }

    .buttons {
        min-height: 38px;
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;

        button {
            min-width: 132px;
        }
    }

    .empty {
        margin: auto;
        min-height: 100px;
    }
</style>